.wrap {
  margin: 3rem 0;
  font-family: var(--font-secondary);
}

.content {
  width: 100%;
  display: flex;

  color: var(--white);

  &__text {
    width: 70%;
    padding: 3.75rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 1rem;

    background-color: var(--primary);
  }

  &__title {
    margin-bottom: 2.5rem;
    width: clamp(346px, 30rem, 80%);
    font-size: 2rem;
    font-family: var(--font-main);

    color: var(--white);
  }

  &__row {
    display: flex;
    flex-direction: column;
    gap: 2rem;
  }

  &__btn {
    align-self: flex-start;
  }

  &__descr {
    font-size: 1rem;
    line-height: 150%;
  }

  &__image {
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

@media screen and (max-width: 768px) {
  .wrap {
    margin-top: 0;
    padding: 40px 0 24px;
    background-color: var(--primary);
  }
  .content {
    &__text {
      width: 100%;
      padding: 0 0.5rem;
    }

    &__title {
      width: 100%;
      font-size: 1.75rem;
    }

    &__row {
      flex-direction: column-reverse;
    }

    &__btn {
      align-self: normal;
    }

    &__image {
      display: none;
    }
  }
}
